<template>
  <div>
    <cpntContent>
        <template slot="cpnt">DyMessage</template> 
        <template slot="name">全局提示</template>
        <template slot="introduction">在页面中半透明提示，用于消息通知、加载提示、操作结果提示等场景。</template>
        <template slot="more">
            <h1 class="fontTitle">代码演示</h1>

