              <template slot="code">
  <codeField>
&lt;button @click="showMessage"&gt;点击我&lt;/button&gt;
</codeField>
<codeField>
&lt;script&gt;<br>
export default {<br>
  methods: {<br>
    showMessage () {<br>
      this.$message({<br>
        message: '我是一个message 消息提示',<br>
        duration: 3000,<br>
        autoClose: false,<br>
        position: 'center',<br>
        type: 'success'<br>
      })<br>
    },<br>
  }<br>
}<br>
&lt;/script&gt;<br>
</codeField>
